$z-profile: 5;
$z-list: 4;
$z-monster: 3;

@keyframes flowerDance {
	0% {
		transform: rotate(0deg) skewY(0deg);
	}
	100% {
		transform: rotate(0deg) skewY(0deg);
	}

}
@keyframes profileBounce {
	0% {
		top: -122px;
	}
	60% {
		top: -114px;
	}
	100% {
		top: -122px;
	}
}

